body {
  margin: 0;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif; }
  body .generalWrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 2fr;
    grid-template-rows: 100px 865px;
    max-width: inherit;
    width: 100%;
    background-color: white;
    background-image: url(1450d1c038d29c757bcd7461d4c01ee7.png);
    background-position: 100px 100px;
    background-repeat: no-repeat; }
    body .generalWrapper .text {
      margin: 12px;
      display: inline-block;
      background-color: white; }
    body .generalWrapper .headWrap {
      display: grid;
      grid-column: 1 / 4;
      grid-row: 1 / 2;
      grid-template-columns: 1fr 2fr 1fr;
      grid-template-rows: 1fr 2fr;
      justify-items: center;
      border: 1px solid black; }
      body .generalWrapper .headWrap .orderCountWrap {
        grid-column: 1 / 2;
        grid-row: 1 / 2;
        width: 100%; }
      body .generalWrapper .headWrap .cookCountWrap {
        grid-row: 1 / 2;
        grid-column: 3 / 4;
        width: 100%; }
      body .generalWrapper .headWrap .cookScreen {
        grid-row: 2 / 3;
        grid-column: 2 / 3;
        width: 100%;
        border: 3px solid red;
        display: flex; }
        body .generalWrapper .headWrap .cookScreen .cookNotWorksIcon {
          width: 50px;
          height: 50px;
          margin: 0 5px;
          background-image: url(f53ce78fe83967de6aa1568fd7c132c9.png); }
        body .generalWrapper .headWrap .cookScreen .cookWorksIcon {
          width: 50px;
          height: 50px;
          margin: 0 5px;
          background-image: url(ea68f816ea13c372cff8e58d3341f59a.png); }
      body .generalWrapper .headWrap .button {
        margin: 5px;
        border: none;
        background-repeat: no-repeat;
        background-color: white;
        outline: none; }
      body .generalWrapper .headWrap .button:hover {
        background-color: #00aeff; }
      body .generalWrapper .headWrap .button:active {
        background-color: red; }
      body .generalWrapper .headWrap .addCookBtn {
        grid-row: 2 / 3;
        grid-column: 3 / 4;
        width: 46px;
        background-image: url(e3d7e8fb85ec7eb60d35fc83700db832.png); }
      body .generalWrapper .headWrap .deleteCookBtn {
        grid-row: 2 / 3;
        grid-column: 1 / 2;
        width: 46px;
        background-image: url(a2b48dd55dbc6d1591b18dab2cec846b.png); }
    body .generalWrapper .mainWrap {
      display: grid;
      grid-column: 1 / 4;
      grid-row: 2 / 3;
      grid-template-columns: 1fr 1fr 1fr;
      justify-items: center; }
      body .generalWrapper .mainWrap .label {
        text-align: center; }
      body .generalWrapper .mainWrap .queueWrap {
        grid-column: 1 / 2;
        grid-row: 1 / 2;
        width: 100%;
        border: 1px solid black;
        text-align: center; }
        body .generalWrapper .mainWrap .queueWrap .text {
          width: 18px;
          border: 1px solid black; }
      body .generalWrapper .mainWrap .processWrap {
        grid-column: 2 / 3;
        grid-row: 1 / 2;
        width: 100%;
        border: 1px solid black;
        text-align: center; }
        body .generalWrapper .mainWrap .processWrap .text {
          width: 18px;
          border: 1px solid black; }
      body .generalWrapper .mainWrap .readyOrderWrap {
        grid-column: 3 / 4;
        grid-row: 1 / 2;
        width: 100%;
        border: 1px solid black;
        text-align: center; }
        body .generalWrapper .mainWrap .readyOrderWrap .text {
          width: 18px;
          border: 1px solid black; }
      body .generalWrapper .mainWrap .reviewWrap {
        grid-column: 1 / 4;
        grid-row: 2 / 3;
        width: 100%;
        border: 1px solid black; }
    body .generalWrapper .orderInfoWrap {
      display: grid;
      grid-column: 4 / 5;
      grid-row: 1 / 3;
      grid-template-rows: 2fr 1fr;
      justify-items: center;
      border: 1px solid black; }
      body .generalWrapper .orderInfoWrap .orderInfoContainer {
        grid-row: 1 / 2;
        width: 100%;
        background-color: aliceblue; }
        body .generalWrapper .orderInfoWrap .orderInfoContainer .orderId {
          background-color: #3ef0f0;
          width: 90%;
          height: 50px;
          margin: 5px auto;
          text-align: center;
          color: red; }
          body .generalWrapper .orderInfoWrap .orderInfoContainer .orderId h2 {
            padding: 10px;
            margin: 0; }
        body .generalWrapper .orderInfoWrap .orderInfoContainer .orderIngredients {
          text-shadow: 0 -1px 4px #FFF, 0 -2px 10px #ff0, 0 -10px 20px #ff8000, 0 -18px 40px #F00;
          background-image: url(4b9843c62ee8870fd7a27a3745b88718.jpg);
          background-position: center;
          background-repeat: no-repeat;
          height: 100%;
          margin: 5px auto;
          text-align: center; }
          body .generalWrapper .orderInfoWrap .orderInfoContainer .orderIngredients h2 {
            margin: 0;
            padding: 10px;
            font-size: xx-large; }
          body .generalWrapper .orderInfoWrap .orderInfoContainer .orderIngredients .ingredientText {
            font-size: 30px;
            text-decoration: overline; }
      body .generalWrapper .orderInfoWrap .orderReviewContainder {
        grid-row: 2 / 3;
        width: 100%;
        background-color: beige; }
